.sidebar {
    margin: 0;
    padding: 0;
    width: 200px;
    background-color: #3639ba;
    position: fixed;
    height: 100%;
    overflow: auto;
  }
  

  .sidebar a {
    display: block;
    color: black;
    padding: 16px;
    text-decoration: none;
  }
  


  .sidebar a:hover:not(.active) {
    background-color: #555;
    color: white;
  }
  
  
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.form {
    border: 3px solid #0b190b;
    max-width: 500px;
    margin: auto;
    padding: 2%;
    margin-top: 7%;
   
   
    background-color: rgb(181, 245, 245);
  }
  .form span{
    color: #2d7c5f;
    
    font-size: 2rem;
    font-weight: 600;
    
    
    
  }
  .error{
    color: red;
  }
  
 
  input[type=email], input[type=password] {
    width: 60%;
    padding: 12px 20px;
    margin: 8px 0;
    display: inline-block;
    border: 1px solid rgb(122, 120, 120);
    box-sizing: border-box;
  }
  
  /* Set a style for all buttons */
  button {
    background-color: #184031;
    color: white;
    padding: 14px 20px;
    margin: 8px 0;
    border: none;
    cursor: pointer;
    width: 60%;
  }
  
  /* Add a hover effect for buttons */
  button:hover {
    opacity: 0.8;
  }
  
  
  

